#countdown {
    border-radius: 20px;
    width: 450px;
    padding: 20px;
    background-color: rgb(255 255 255 / 13%);
    display: inline-block;
    text-align: center;
    margin: auto;
    margin-top: 30px;
    color: #fff;
  }
  #countdown .box {
    padding: 10px;
    border-right: solid 1px rgba(255, 255, 255, 0.2);
  }
  #countdown .date-item:last-child .box {
    border-right-color: transparent;
  }
  #countdown .box p, #countdown h1{
    font-size: 45px;
    font-weight: bold;
    margin: 0;
    font-family: "Montserrat";
  }
  #countdown .box .text {
    font-size: 13px;
    font-family: sans-serif;
  }
  
  .date-item {
    width: 25%;
    float: left;
  }
  
  .counter-input {
    font-size:25px
  }
  .counter-input input{
    color: white;
  }
  
  @media (max-width: 768px) {
    #countdown .box p, #countdown h1 {
      font-size: 35px;
    }
    .to-mintpage {
      font-size: 24px;
    }
  }
  
  @media (max-width: 576px) {
    #countdown {
      width: 80vw;
    }
  }
  
  @media (max-width: 360px) {
    .date-item {
      width: 50%;
    }
    #countdown .date-item:nth-child(2) .box {
      border-right-color: transparent;
    }
  }
  
  @media (max-width: 210px) {
    .date-item {
      width: 100%;
    }
    #countdown .date-item .box {
      border-right-color: transparent;
    }
  }