// header.js
#myHeader {
  width: 100%;
  height: 10vh;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  position: absolute;
  background: #00000000;
  transition: all 0.6s ease;
  &.sticky {
    position: fixed;
    top: 0;
    width: 100%;
    background: #00000054;
    border-bottom: 0;
    transition: all 0.6s ease;
    &.white {
      background: #00000054;
      box-shadow: 0 4px 10px 0 rgba(10, 10, 10, 0.8);
    }
  }
  .mainside {
    width: 90%;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: space-between;
    .menuItem {
      display: flex;
      margin: auto;
      .menulink {
        margin-right: 30px;
        color: white;
        font-family: "Montserrat", sans-serif !important;
        font-size: 18px;
        text-decoration: none;
        cursor: pointer;
      }
    }
    .menuItemmint {
      display: flex;
      .menulink {
        color: white;
        font-family: "Montserrat", sans-serif !important;
        font-size: 18px;
        text-decoration: none;
        cursor: pointer;
      }
    }
    .menuBtnArea {
      display: flex;
      .btn-main {
        color: white;
        text-align: center;
        border-radius: 30px;
        padding: 10px 30px;
        box-shadow: 0 0 0 0 white;
        background: linear-gradient(50deg, #6f583f98, #6d737183);
        // animation: pulse 3s infinite 3s cubic-bezier(0.25, 0, 0, 1);
        cursor: pointer;
        span {
          font-size: 16px;
          align-items: center;
          img {
            width: 25px;
          }
        }
      }
      .space {
        width: 10px;
      }
      .btn-icons {
        display: flex;
        margin: auto;
        div {
          cursor: pointer;
          position: relative;
          border: 3px solid transparent;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 50px;
          height: 50px;
          background: linear-gradient(50deg, #6f583f98, #6d737183);
          margin: 0 5px 0 5px;
          a {
            z-index: 2;
          }
        }
        div::after {
          content: "";
          position: absolute;
          top: -5px;
          bottom: -5px;
          left: -5px;
          right: -5px;
          z-index: 1;
          border-radius: 50%;
          background: linear-gradient(to right, #b4b5b3, #363932);
        }
      }
    }
  }
}
@media (max-width: 992px) {
  #myHeader .mainside .menuItem {
    display: none !important;
  }
  .ToggleBtn {
    display: block;
  }
  #myHeader .mainside {
    justify-content: space-between;
  }
}

