.experience-left-arrow {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(-50%, -50%);
  right: auto;
  bottom: auto;
  display: block;
  width: 50px;
  max-height: 50px;
  min-height: 50px;
  min-width: 50px;
  border-style: solid;
  border-width: 1px;
  border-color: #eaeaea;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 0 20px 48px 0 rgb(27 27 27 / 6%);
  -webkit-transition: background-color 300ms ease, color 300ms ease,
    border-color 300ms ease, box-shadow 300ms ease, -webkit-transform 300ms ease;
  transition: background-color 300ms ease, color 300ms ease,
    border-color 300ms ease, box-shadow 300ms ease, -webkit-transform 300ms ease;
  transition: background-color 300ms ease, color 300ms ease,
    border-color 300ms ease, box-shadow 300ms ease, transform 300ms ease;
  transition: background-color 300ms ease, color 300ms ease,
    border-color 300ms ease, box-shadow 300ms ease, transform 300ms ease,
    -webkit-transform 300ms ease;
  color: #1a1e1c;
  font-size: 28px;
  z-index: 5;
  cursor: pointer;
}
.experience-left-arrow:hover {
  background-color: rgba(255, 255, 255, 0.6);
}
.slider-left {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 1em;
  height: 1em;
  font-style: normal;
  font-weight: 1000;
  font-variant: normal;
  font-size: 30px;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
