#team {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  // margin: 40px 0;
}
#team .item {
  position: relative;
  // width: 100%;
  // height: 400px;
  // margin: 20px;
  overflow: hidden;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#team .contentcontainer {
  position: relative;
  bottom: 0;
}
#team .item .content {
  position: absolute;
  bottom: -130px;
  --gap: 40px;
  width: calc(100%);
  // margin:10px;
  height: 130px;
  display: flex;
  visibility: hidden;
  justify-content: center;
  align-items: center;
  z-index: 10;
  flex-direction: column;
  backdrop-filter: blur(15px);
  box-shadow: 0 -10px 10px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  transition: bottom 0.5s;
  transition-delay: 0.65s;
}
#team .item:hover .content {
  bottom: 0;
  transition-delay: 0s;
  visibility: visible;
}
#team .item .content .contentBx h3 {
  text-transform: uppercase;
  color: #fff;
  letter-spacing: 2px;
  font-weight: 500;
  font-size: 18px;
  text-align: center;
  margin: 20px 0 15px;
  line-height: 1.1em;
  transition: 0.5s;
  transition-delay: 0.6s;
  opacity: 0;
  transform: translateY(-20px);
}
#team .item:hover .content .contentBx h3 {
  opacity: 1;
  transform: translateY(0);
}
#team .item .content .contentBx h3 span {
  font-size: 20px;
  font-weight: 300;
  text-transform: initial;
}
#team .item .content .sci {
  position: relative;
  bottom: 10px;
  display: flex;
  justify-content: center;
  margin-top: 10px;
  padding-left: 0 !important;
}
#team .item .content .sci li {
  list-style: none;
  margin: 0 10px;
  transform: translateY(40px);
  display: list-item;
  transition: 0.5s;
  opacity: 0;
  transition-delay: calc(0.2s * var(--i));
}

#team .item:hover .content .sci li {
  transform: translateY(0);
  opacity: 1;
}
#team .item .content .sci li a {
  color: white;
  font-size: 24px;
}
